import React from 'react';
import T from 'prop-types';
import isEmpty from 'lodash/isEmpty';
import { _ } from '@splunk/ui-utils/i18n';
import Heading from '@splunk/react-ui/Heading';
import Markdown from '@splunk/react-ui/Markdown';
import styled from 'styled-components';
import { getOptionDisplayType, getPatternOptions } from '../../.storybook/utils/storybookUtils';

const Option = styled.div`
    margin-top: 20px;
`;
const OptionName = styled.div`
    color: #5ca300;
    padding-bottom: 5px;
`;
const PairContainer = styled.div`
    margin-left: 40px;
    margin-top: 10px;
`;
const PairTitle = styled.span`
    display: inline-block;
    color: #8293a7;
    width: 80px;
`;

const PairValue = styled.span`
    font-family: 'Splunk Platform Mono', 'monospace';
    white-space: pre;
`;

const PropertiesWrapper = styled.div`
    margin-left: ${props => (props.isPatternProperty ? 0 : 40)}px;
    margin-top: 20px;
`;

const PropertiesContainer = styled.div`
    margin-left: ${props => (props.isPatternProperty ? 40 : 0)}px;
    margin-top: 10px;
`;

const PropertiesContent = styled.div`
    margin-left: ${props => (props.isPatternProperty ? 40 : 0)}px;
`;

/**
 * @method isOneOfJsonSchema
 * @param {object} item
 * @returns {boolean}
 */
const isOneOfJsonSchema = item => item.oneOf && Array.isArray(item.oneOf);

/**
 * get Or type as string
 * @method getOrType
 * @param {object} item
 * @param {string} res
 * @returns {string} res  eg: (string | number)
 */
const getOrType = item => {
    let res = '';

    if (isOneOfJsonSchema(item)) {
        res = item.oneOf
            .map(one => {
                if (one.type !== 'array') {
                    return one.type;
                }
                // eslint-disable-next-line no-use-before-define
                return getArrayType(one, '');
            })
            .join(' | ');
    } else if (Array.isArray(item.type)) {
        if (isEmpty(item.default)) {
            return item.type.join(' | ');
        }

        res = item.type.filter(itemType => itemType !== 'string').join(' | ');
        res += ' | ';
        res += `"${item.default}"`;
    }

    return res;
};

/**
 * get the array type as string
 * @method getArrayType
 * @param {object} item
 * @param {string} res
 * @returns {string} res  eg: arrayOf(arrayOf(string | number | arrayOf(string | number)))
 */
const getArrayType = (item, res = '') => {
    if (item.type !== 'array') {
        if (Array.isArray(item.type) || isOneOfJsonSchema(item)) {
            return getOrType(item);
        }

        return item.type;
    }

    if (item.type === 'array') {
        res += 'arrayOf('; // eslint-disable-line no-param-reassign
    }
    res += getArrayType(item.items, ''); // eslint-disable-line no-param-reassign
    res += ')'; // eslint-disable-line no-param-reassign
    return res;
};

/**
 * get the type of this option item
 * @method getType
 * @param {ojbect} item option item
 * @returns {string} type
 */
export const getType = item => {
    if (isOneOfJsonSchema(item)) {
        return `oneOf(${getOrType(item)})`;
    }
    if (item.enum && Array.isArray(item.enum)) {
        const values =
            !isEmpty(item.enum) && typeof item.enum[0] === 'string'
                ? item.enum.map(v => `"${v}"`).join(', ')
                : item.enum.join(', ');

        return `oneOf([${values}])`;
    }

    if (Array.isArray(item.type)) {
        return getOrType(item);
    }

    if (item.type === 'array') {
        if ('items' in item) {
            return `arrayOf(${getType(item.items)})`;
        }
        return 'array';
    }
    return item.type;
};

/**
 * get the default value as string
 * @method getDefaultValue
 * @param {object | boolean | number | string} defaultValue
 * @returns {string} default value
 */
const getDefaultValue = defaultValue => {
    if (typeof defaultValue === 'boolean' || typeof defaultValue === 'number') {
        return defaultValue.toString();
    }
    if (Array.isArray(defaultValue)) {
        const values = defaultValue.map(v => `"${v}"`).join(', ');
        return `[${values}]`;
    }
    return defaultValue;
};

const Pair = ({ title, text }) => (
    <PairContainer>
        <PairTitle>{`${title}:`}</PairTitle>
        <PairValue>{text}</PairValue>
    </PairContainer>
);

Pair.propTypes = {
    title: T.string,
    text: T.string,
};

const PropertyItems = ({ isPatternProperty = false, propsItems }) => (
    <PropertiesContainer isPatternProperty={isPatternProperty}>
        {isPatternProperty ? <PairTitle>{_('[fieldName]:')}</PairTitle> : null}
        <PropertiesContent isPatternProperty={isPatternProperty}>{propsItems}</PropertiesContent>
    </PropertiesContainer>
);

PropertyItems.propTypes = {
    isPatternProperty: T.bool,
    propsItems: T.array,
};

const OptionItem = ({ name, type, desc, defaultValue, enumerable, PropertiesSection }) => (
    <Option>
        <OptionName>{name}</OptionName>
        <Markdown style={{ marginLeft: 40 }} text={desc} />
        <Pair title={_('Type')} text={type} />
        {enumerable?.length > 0 && (
            <Pair
                title={_('oneOf')}
                text={`(${enumerable
                    .map(
                        entry =>
                            `${typeof entry === 'string' ? '"' : ''}${entry}${
                                typeof entry === 'string' ? '"' : ''
                            }`
                    )
                    .join(' | ')})`}
            />
        )}
        {defaultValue && <Pair title={_('Default')} text={defaultValue} />}
        {PropertiesSection}
    </Option>
);

OptionItem.propTypes = {
    name: T.string,
    type: T.string,
    desc: T.string,
    enumerable: T.arrayOf(T.oneOfType([T.string, T.number])),
    defaultValue: T.string,
    PropertiesSection: T.element,
};

OptionItem.default = {
    PropertiesItem: null,
};

const OptionsDoc = ({ options, description }) => (
    <div>
        {options.length === 0 ? (
            <Heading level={4}> No Options</Heading>
        ) : (
            <Heading level={4}>Options</Heading>
        )}
        {description && <div>{description}</div>}
        {Object.entries(options).map(([key, item]) => {
            let type;
            let enumerable = [];
            let PropertiesSection = null;

            // get oneOf property listing
            if (typeof item.anyOf?.[0]?.pattern === 'string') {
                enumerable = getPatternOptions(item.anyOf[0].pattern);
            } else if (item.enum) {
                enumerable = item.enum;
            }

            // get properties for object type
            if (item.type === 'object') {
                type = item.type;

                const { properties } = item;
                const { patternProperties } = item;
                let allProperties = [];
                let isPatternProperty = false;

                // the properties either comes from property field of the option schema item
                // or from the property field of each patternProperties of the option schema item
                // can check table tableFormat and columnFormat as examples
                if (!isEmpty(properties)) {
                    allProperties = [properties];
                } else if (!isEmpty(patternProperties)) {
                    Object.keys(patternProperties).forEach(patternKey => {
                        const propsInPattern = patternProperties[patternKey].properties;
                        if (propsInPattern) {
                            allProperties.push(propsInPattern);
                        }
                    });

                    isPatternProperty = true;
                }

                if (allProperties.length) {
                    const allPropsItems = allProperties.map(props => {
                        const propsItems = Object.keys(props).map(pKey => {
                            const p = props[pKey];
                            let pType = getOptionDisplayType({ ...p.anyOf[0], enum: p.enum });
                            const pDefault = getDefaultValue(p.default);

                            let pEnumerable;
                            // get nested oneOf property listing
                            if (typeof p.anyOf?.[0]?.pattern === 'string') {
                                pEnumerable = getPatternOptions(p.anyOf[0].pattern);
                            } else if (p.anyOf?.[0]?.items?.enum?.length > 0) {
                                // quick fix for nested `columnFormat` enumerables that replaces enums within brackets with raw JS types
                                pType = pType.replace(/\((.+?)\)/g, 'string');
                                pEnumerable = p.anyOf[0].items.enum;
                            } else if (p.anyOf?.[0]?.items?.items?.enum?.length > 0) {
                                // quick fix for nested `tableFormat` enumerables that replaces enums within brackets with raw JS types
                                pType = pType.replace(/\((.+?)\)/g, 'string');
                                pEnumerable = p.anyOf[0].items.items.enum;
                            } else if (p.enum) {
                                pEnumerable = p.enum;
                            }

                            // use the JS types (e.g. string, number) of the nested property if the nested property is an enumerable
                            if (pEnumerable?.length) {
                                pType = p.anyOf?.[0]?.type !== 'array' ? p.anyOf?.[0]?.type : pType;
                                if (Array.isArray(pType)) {
                                    pType = pType.join(' | ');
                                }
                            }

                            return (
                                <OptionItem
                                    key={pKey}
                                    name={pKey}
                                    type={pType}
                                    enumerable={pEnumerable}
                                    desc={p.description}
                                    defaultValue={pDefault}
                                />
                            );
                        });
                        return (
                            <PropertyItems
                                key={propsItems}
                                isPatternProperty={isPatternProperty}
                                propsItems={propsItems}
                            />
                        );
                    });

                    PropertiesSection = (
                        <PropertiesWrapper isPatternProperty={isPatternProperty}>
                            {allPropsItems}
                        </PropertiesWrapper>
                    );
                }
            } else if (enumerable?.length) {
                // use the JS types (e.g. string, number) of the property if the property is an enumerable
                type = item.anyOf?.[0]?.type;
                if (Array.isArray(type)) {
                    type = type.join(' | ');
                }
            } else {
                type = getOptionDisplayType({ ...item.anyOf[0], enum: item.enum });
            }

            return (
                <OptionItem
                    key={key}
                    name={key}
                    // item.anyOf in the config.ts is a tuple which holds [{ type, pattern, items }, { type, pattern: DYNAMIC_OPTIONS_PATTERN }]
                    type={type}
                    enumerable={enumerable}
                    desc={item.description}
                    defaultValue={getDefaultValue(item.default)}
                    PropertiesSection={PropertiesSection}
                />
            );
        })}
    </div>
);

OptionsDoc.propTypes = {
    options: T.object.isRequired,
    description: T.string,
};

export default OptionsDoc;
