import { get, omit, reduce, toNumber, isString } from 'lodash';
import { OptionsSchema } from '../../src/common/interfaces/OptionsSchema';
import { isDynamicOption } from '../../src/common/utils/configUtils';

interface ArgTypeControl {
    type: string;
    options?: string[];
    min?: number;
    max?: number;
}

interface ArgTypeEntry {
    description?: string;
    control: ArgTypeControl;
    name: string;
    defaultValue?: any;
    type: { required: boolean };
    optionType: string;
    table: {
        type: { summary: string };
        defaultValue?: { summary: any };
        category?: string;
        subcategory?: string;
        disable?: boolean;
    };
}

export interface ArgTypes {
    [key: string]: ArgTypeEntry;
}

type StoriesList = RegExp | string[];

/**
 * Returns type options from pattern
 * @example
 * // returns ['fixed', 'absolute', 'off']
 * getPatternOptions('\$(\w+:)?([^$|:]+?)(\|[|\w]+)?\$|^fixed$|^absolute$|^off$')
 * @example
 * // returns []
 * getPatternOptions('^>.*')
 * @param {string} patternString - visualization option type pattern
 * @return {string[]} array of type options
 */
export const getPatternOptions = (patternString): string[] =>
    patternString.replace(/\$/g, '').split('|^').slice(1);

/**
 * Returns Storybook display type for an option
 * @param {object} option - visualization option
 * @return {string} Storybook display type
 */
export const getOptionDisplayType = (option): string => {
    const { oneOf, anyOf, type, enum: en, pattern, items } = option;
    let displayTypes: string[] = [];

    // wrap values in quotes only if enum or pattern is string
    const conditionalQuote = type === 'string' ? '"' : '';
    if (en && Array.isArray(en)) {
        displayTypes = en.map(e => {
            const enumQuote = typeof e === 'string' ? '"' : '';
            return `${enumQuote}${e}${enumQuote}`;
        });
    } else if (type === 'array' && items) {
        return `${getOptionDisplayType(items)}[]`;
    } else if (anyOf && Array.isArray(anyOf)) {
        return getOptionDisplayType(anyOf[0]);
    } else if (oneOf && Array.isArray(oneOf)) {
        displayTypes = oneOf.map(item => getOptionDisplayType(item));
    } else if (Array.isArray(type)) {
        displayTypes = type;
    } else if (pattern) {
        displayTypes = getPatternOptions(pattern).map(p => `${conditionalQuote}${p}${conditionalQuote}`);
    }

    if (displayTypes.length > 0) {
        return `(${displayTypes.join(' | ')})`;
    }
    return type;
};

/**
 * Returns Storybook control type for an option
 * @param {object} option - visualization option
 * @param {string} optionKey - visualization option key
 * @return {ArgTypeControl} Storybook control object
 */
export const getOptionControl = (option, optionKey): ArgTypeControl => {
    const { anyOf } = option;
    const schema = Array.isArray(anyOf) && anyOf.length ? anyOf[0] : option;

    const type = Array.isArray(schema.type) ? schema.type[0] : schema.type;
    const { maximum, minimum, enum: en } = schema;

    const isColorOption = new RegExp(/color/, 'i').test(optionKey) && type !== 'array';

    if (isColorOption) {
        return { type: 'color' };
    } else if (en && Array.isArray(en)) {
        return { type: 'select', options: en };
    } else if (anyOf && type === 'string' && anyOf[0].pattern) {
        return { type: 'select', options: getPatternOptions(anyOf[0].pattern) };
    } else if (type === 'number' && (minimum || maximum)) {
        return {
            type: 'range',
            ...(typeof minimum === 'number' && { min: minimum }),
            ...(typeof maximum === 'number' && { max: maximum }),
        };
    } else if (type === 'string') {
        return { type: 'text' };
    }

    return { type };
};

/**
 * platform specific visualization option argTypes
 */
const objectTableControl = { control: { type: 'object' } };
const disableTableControl = { table: { disable: true } };
export const platformOptionArgTypes = {
    context: objectTableControl,
    dataSources: objectTableControl,
    dataSource: disableTableControl,
    options: disableTableControl,
    onEventTrigger: { action: 'eventTrigger', ...disableTableControl },
    hasEventHandlers: disableTableControl,
};

/**
 * Returns formatted Storybook argTypes for a visualization from its options schema
 * @param {OptionsSchema} options - visualization options
 * @param {string[]} requiredProps - required visualization props
 * @param {boolean} isPlatformViz - indicates if pure or platform visualization
 * @param {string} subcategory - nested property parent option name
 * @return {ArgTypes} Storybook argTypes
 */
export const optionsSchemaToArgTypes = ({
    options = {},
    requiredProps = [],
    isPlatformViz = false,
    subcategory = null,
}: {
    options: OptionsSchema;
    requiredProps: string[];
    isPlatformViz?: boolean;
    subcategory?: string;
}): ArgTypes => {
    const validObjectTypeOptions = ['lineDashStylesByField', 'seriesColorsByField', 'seriesColorsByValue'];
    let argTypes: ArgTypes = {};
    Object.keys(options).forEach(optionKey => {
        const option = get(options, optionKey);
        if (option.type === 'object' && !validObjectTypeOptions.includes(optionKey)) {
            // do not support controls for patternProperties, only nested properties
            if (option.properties) {
                argTypes = {
                    ...argTypes,
                    ...optionsSchemaToArgTypes({
                        options: option.properties as OptionsSchema,
                        isPlatformViz,
                        requiredProps,
                        subcategory: optionKey,
                    }),
                };
            }
        } else {
            const { description, default: optionDefault } = option;
            const optionControl = getOptionControl(option, optionKey);
            const control = isPlatformViz ? { type: 'text' } : optionControl;
            const required = requiredProps.includes(optionKey);

            let defaultValue = optionDefault;
            if (!isPlatformViz && isDynamicOption(optionDefault as string)) {
                defaultValue = null;
            }

            argTypes[optionKey] = {
                description,
                control,
                name: optionKey,
                defaultValue,
                type: { required },
                optionType: optionControl.type,
                table: {
                    type: { summary: getOptionDisplayType(option) },
                    defaultValue: { summary: defaultValue },
                    ...(subcategory && {
                        category: 'Nested Options',
                        subcategory,
                    }),
                },
            };
        }
    });

    if (isPlatformViz) {
        Object.assign(argTypes, platformOptionArgTypes);
    }
    return argTypes;
};

/**
 * Helper which converts string visualization options to their required type
 * @param {any} value - option values
 * @param {string} expectedType - expected option type
 * @return {any} correctly typed option value
 */
export const transformOptionType = (value, expectedType = 'string'): any => {
    const isStringValue = !isDynamicOption(value) && isString(value);
    if (isStringValue && expectedType === 'boolean') return value == 'true';
    else if (isStringValue && expectedType === 'number') return toNumber(value);
    return value;
};

// stringify these fields for storybook controls' support
const FieldsToStringify = [
    'legendLabels',
    'lineDashStylesByField',
    'seriesColors',
    'seriesColorsByField',
    'seriesColorsByValue',
];

/**
 * Helper which returns platform visualization options from Storybook controls
 * Utilizes argTypes to determine valid and nested options
 * @param {object} args - visualization story args
 * @param {ArgTypes} argTypes - visualization argTypes
 * @return {object} visualization options
 */
export const getOptionsFromArgTypes = (args: Record<string, any>, argTypes: ArgTypes): Record<string, any> =>
    reduce(
        argTypes,
        (accum, val, option) => {
            const subcategory = get(val, 'table.subcategory');
            const { optionType, defaultValue } = argTypes[option];
            let controlledOption = transformOptionType(get(args, option), optionType);
            // filter options from react & json if set to default value or is undefined
            if (optionType && controlledOption != null && controlledOption !== defaultValue) {
                // parse the value if option is stringified as part of flattenVizOptions
                if (FieldsToStringify.includes(option)) {
                    try {
                        controlledOption = JSON.parse(controlledOption);
                    } catch (e) {
                        controlledOption = null;
                    }
                }
                if (subcategory) {
                    if (!accum[subcategory]) accum[subcategory] = {};
                    accum[subcategory][option] = controlledOption;
                } else accum[option] = controlledOption;
            }
            return accum;
        },
        { ...omit(args.options, Object.keys(argTypes)) }
    );

/**
 * Helper which flattens objects single depth level
 * @param {object} options - visualization options object
 * @return {object} flattened visualization options
 */
export const flattenVizOptions = (options: Record<string, any>): Record<string, any> => {
    const nonFlattenOptions = new Set(['y2Fields', 'overlayFields', 'gaugeRanges']);

    return reduce(
        options,
        (accum, val, option) => {
            if (!platformOptionArgTypes[option]) {
                if (typeof val === 'object' && FieldsToStringify.includes(option))
                    accum[option] = JSON.stringify(val);
                else if (typeof val === 'object' && !nonFlattenOptions.has(option))
                    accum = { ...accum, ...val };
                else accum[option] = val;
            }
            return accum;
        },
        {}
    );
};
/**
 * Returns value based on Storybook publishing status
 * @param {T} publishedStories - stories to include in published Storybook
 * @param {T} localStories - stories to include in local Storybook
 * @return {T} one of the two params
 */
export const getStoriesToInclude = <T>(publishedStories: T, localStories: T): T =>
    process.env.STORYBOOK_PUBLISH ? publishedStories : localStories;

/**
 * Returns component level stories to include, defaults to innclude all stories
 * @param {StoriesList} stories - stories to include in published Storybook
 * @return {object} CSF includeStories config
 */
export const componentStoriesToInclude = (stories: StoriesList): object =>
    getStoriesToInclude({ includeStories: stories }, {});
